* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 2rem;
}

input {
  width: 100%;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 0.2rem;
  margin: 0 1rem 1rem 0;
  padding: 1rem;
}

.card img {
  display: block;
  max-width: 10rem;
}
